@import '../styles/vars';
@import '../styles/mixins';

.hero {
  background-image: url(../assets/svg/bgnHero.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  max-width: 1920px;
  // margin-bottom: 241px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: $tablet2) and (max-width: $to-tablet) {
    margin-bottom: 120px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 180px;
  }

  @media screen and (min-width: $desktop) {
    margin-bottom: 241px;
  }
}

.content {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;

  // padding-top: 142px;
  // padding-bottom: 232px;

  @media screen and (min-width: $tablet2) and (max-width: $to-tablet) {
    padding-top: 120px;
    padding-bottom: 123px;
  }

  @media screen and (min-width: $tablet) {
    padding-top: 142px;
    padding-bottom: 213px;
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 232px;
  }
}

.title {
  // font-size: 64px;
  font-weight: 900;

  @media screen and (min-width: $tablet2) and (max-width: $to-tablet) {
    font-size: 45px;
  }

  @media screen and (min-width: $tablet) {
    font-size: 64px;
  }
}

.subtitle {
  @include font($fontSize: 18px);

  @media screen and (min-width: $tablet2) and (max-width: $to-tablet) {
    font-size: 22px;
    width: 303px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 24px;
  }
}

.img {
  position: absolute;
  // height: 646px;
  top: 49px;
  right: 0px;

  @media screen and (min-width: $tablet2) {
    height: 430px;
  }

  @media screen and (min-width: $tablet) {
    height: 550px;
  }

  @media screen and (min-width: $desktop) {
    height: 646px;
  }
}
